import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import { $http } from '@escook/request-miniprogram'
uni.$http = $http
$http.baseUrl = 'https://api-hmugo-web.itheima.net'
$http.beforeRequest = function(options){
  uni.showLoading({
    title:'数据加载中...'
  })
}
$http.afterRequest = function(options){
  uni.hideLoading()
}

uni.$showMsg = function(title='数据请求失败',duration=1500){
   uni.showToast({
      title,
      duration,
      icon:'none'
   })
}


import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App
})
app.$mount()